import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			 this.$http.post("/mem/applogin/loginout",{"PERSONID":""}).then(res=>{
				console.log(res.data)
				
			}).catch(err=>{
				uni.hideLoading();
				uni.showToast({
					title:err,
					icon:"none"
				})
			}); 
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		}
	}
}
